import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListsComponent } from './lists/lists.component';
import { CreateComponent } from './create/create.component';
import { Role } from '@app/enum/app_enum';
import { AuthGuardService } from '@app/core/guards/auth-guard.service';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BasicComponentsModule } from '@app/shared/basic-components/basic-components.module';

const routes: Routes = [
  {
    path: 'management',
    component: ListsComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin] }
  },
  {
    path: 'create',
    component: CreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin] }
  },
  {
    path: 'management/edit/:id',
    component: CreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin] }
  }
];

const comLists=[
  ListsComponent,
  CreateComponent,
];

@NgModule({
  declarations: [comLists],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule,
    BasicComponentsModule,
  ],
  exports:[
    comLists
  ]
})
export class LogoModule { }
