import { Component, OnInit } from '@angular/core';
import { LessonplanService } from '@app/core/services/serviceIndex';
import { ActionLabel, AdminSideMenu, Label } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-lists',
  templateUrl: './lists.component.html',
  styleUrls: ['./lists.component.css']
})
export class ListsComponent implements OnInit {
  public routeDirLists = {
    routeName: [AdminSideMenu.Logo],
    routeLink: []
  };

  public tableHeader = [Label.name,Label.type,Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete];
  public dataLists: any = {
  };
  public objectLabel: any = ["name", "type"];

  public limit: any = 10;
  public page: any = 1;
  public totalCount: any;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public actionLinks: any = {
    editLink: "admin/logo/management/edit",
    deleteLink: "logos",
    idKeyname: "logoId",
  }
  public searchData: any;
  public subscription: Subscription;


  constructor(private _service: LessonplanService) { }

  ngOnInit(): void {
    this.getLogoList();
  }

  onsearch(search: string) {
    console.log(search);
    this.searchData = search;
    this.getLogoList();
  }

  getLogoList(){
    this.subscription = this._service.getLogoList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
    .subscribe((res) => {
      console.log(res);
      this.dataLists.data = res.logos;
      this.totalCount = res.totalCount;
      this.dataLists.tableHeader = this.tableHeader;
      this.dataLists.actionBtns = this.actionBtns;
      this.dataLists.objectLabel = this.objectLabel;
      this.dataLists.btnLink = this.actionLinks;
    })
  }

  onFilter(filter: any) {
    this.filter = filter;
    this.getLogoList();
  }
  
  pageChanges(e: any) {
    console.log(e);
    this.page = e;
    this.getLogoList();
  }

}
