import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthenticationService } from '@app/core/authentication/authentication.service';
import { LessonplanService } from '@app/core/services/serviceIndex';
import { ActionLabel, AdminSideMenu, EssentialRoute } from '@app/enum/app_enum';
import { Logo } from '@app/shared/models/logo';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {

  public routeDirLists: any = {
    routeName: [AdminSideMenu.Logo],
    routeLink: [EssentialRoute.LogoList]
  };
  public submitted: boolean;
  public logoForm: FormGroup;
  public file: any;
  public logoId: any;
  public subscription: Subscription;


  constructor(private formBuilder: FormBuilder, private _ser: LessonplanService,
    private _activeRoute: ActivatedRoute, private _auth: AuthenticationService, private _router: Router) { }

  ngOnInit(): void {
    this._activeRoute.params.subscribe(params => {
      this.logoId = params.id;
      this.logoForm = this.formBuilder.group({
        name: ["", [Validators.required]],
        type: ["", [Validators.required]],
      });
      if (this.logoId) {
        this.getLogoDetail();
        this.routeDirLists.routeName.push(ActionLabel.edit);
      } else this.routeDirLists.routeName.push(ActionLabel.create)
    });
  }

  getLogoDetail() {
    this.subscription = this._ser.getLogoDetail(this.logoId)
      .subscribe((res:Logo) => {
        let temp = res;
        this.logoForm = this.formBuilder.group({
          name: [temp.name, [Validators.required]],
          type: [temp.type, [Validators.required]],
        });
        this.file=res.image;
      })
  }

  onSubmit() {
    this.submitted = true
    if (this.logoForm.invalid || !this.file) return;
    else {
      if (!this.logoId)
        this.onCreate();
      else this.onEdit();
    }
  }

  onCreate() {
    let logo = new Logo();
    logo.name = this.logoForm.value.name;
    logo.type = this.logoForm.value.type;
    logo.image = this.file;
    this.subscription = this._ser.onCreateLogo(logo)
      .subscribe((res) => {
        this.submitted = false;
        this.cancelLogo();

      });
  }

  onEdit() {
    let logo = new Logo();
    logo.name = this.logoForm.value.name;
    logo.image = this.file;
    logo.logoId = this.logoId;
    this.subscription = this._ser.onUpdateLogo(logo)
      .subscribe((res) => {
        this.submitted = false;
        this.cancelLogo();
      });
  }
  getfile(file: any) {
    this.file = file;
  }

  // convenience getter for easy access to form fields
  get f() { return this.logoForm.controls; }

  cancelLogo() {
    this._router.navigateByUrl( EssentialRoute.LogoList);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
